.founders{background-image:url(./img/Bg.png);display:grid;height:67rem;background-size:cover;grid-template-rows:repeat(6, 1fr);grid-template-columns:.5fr 1fr}.founders-title{grid-row-start:2;margin:0;font-family:"IBM Plex Mono",monospace;font-size:48px;font-weight:600;line-height:59px;color:#fff;background-color:#bf1616;width:509px;height:99px;border-radius:5px;text-align:center;padding-top:1rem;letter-spacing:2px}.founders-img{grid-row:3/6;justify-self:center;display:flex;flex-wrap:wrap;width:15.5rem}.founders-img p{position:relative;font-family:"IBM Plex Mono",monospace;font-size:16px;font-weight:500;line-height:20px;width:248px;height:97px;color:#fff;background-color:#bf1616;padding-left:2.5em;padding-top:1em;margin:0}.founders-img p::before{content:"";position:absolute;top:25px;left:15px;font-size:48px;font-family:"Montserrat",serif}.founders-container{position:relative;display:grid;justify-items:center;justify-self:center;grid-template-rows:1fr 1fr .5fr 1fr 1fr;grid-template-columns:1fr .1fr;background-color:#fffbf6;height:50rem;padding:1.5rem 0 3rem 1.5rem}.founders-container--ornament{grid-row:1/3;grid-column:2/5;justify-self:end}.founders-container--name{font-family:"Kyiv Region",sans-serif;font-size:24px;font-weight:700;line-height:23px;color:#bf1616;margin:0;align-self:center;width:28.5rem;text-transform:uppercase}.founders-container--title{font-family:"IBM Plex Mono",monospace;font-size:20px;font-weight:500;line-height:24px;color:#bf1616;margin:0;align-self:start;width:28.5rem}.founders-container--info{font-family:"IBM Plex Mono",monospace;font-size:18px;font-weight:400;line-height:22px;grid-row-start:4;grid-column-start:1;margin:0;align-self:center;width:28.5rem}.founders-container--text{grid-column-start:1;grid-row-start:5;font-family:"IBM Plex Mono",monospace;font-size:18px;font-weight:400;line-height:22px;margin:0;align-self:center;width:28.5rem}.founders-carousel{display:flex;scroll-behavior:smooth;position:relative;grid-row:2/6;grid-column:2/3;width:36.8rem;margin:0 auto}.carousel{height:49.125rem}.carousel,.slide{text-align:start !important}.carousel-root{grid-row:2/5}.carousel.carousel-slider .control-arrow{padding:3px !important}@media screen and (max-width: 1024px){.founders{background-image:url(./img/tabletBg.png);height:150rem;grid-template-columns:1fr;grid-template-rows:1fr 1fr}.founders-data{display:grid;grid-template-rows:1fr 1fr;row-gap:3rem;padding-bottom:5rem}.founders-title{font-size:64px;line-height:61px;grid-column:1/3;grid-row:1;width:100vw;text-align:start;align-self:center;padding-left:2rem}.founders-carousel{grid-column:1;padding-top:0;display:grid}.founders-img{display:none}.founders-container{grid-template-columns:1fr .1fr;grid-template-rows:auto;padding-top:0;height:60rem;width:39rem}.founders-container--ornament{overflow:hidden;width:36rem;grid-row:2;align-self:center;grid-column:1}.founders .founders-container--name{text-align:start;font-size:28px;line-height:26px;grid-column:1}.founders .founders-container--title{text-align:start;font-size:24px;line-height:29px;grid-row:4;grid-column:1}.founders .founders-container--info{text-align:start;grid-row:5;font-size:24px;line-height:29px}.founders .founders-container--text{text-align:start;grid-row:6;font-size:24px;line-height:29px}}@media screen and (max-width: 480px){.founders{max-width:480px;height:108rem}.founders-container{grid-template-columns:1fr;grid-template-rows:auto;height:40rem;width:95vw;padding:0 0 1rem}.founders-container--ornament{width:95vw;text-align:center}.founders-title{font-size:38px;line-height:40px}.founders-data{display:grid;grid-template-rows:1fr 1fr;row-gap:3rem;padding-bottom:5rem}.founders .founders-container--name{width:95vw;padding-left:.5rem;font-size:16px;line-height:15px}.founders .founders-container--title{width:95vw;padding-left:.5rem;font-size:14px;line-height:17px}.founders .founders-container--info{width:95vw;padding-left:.5rem;font-size:14px;line-height:17px}.founders .founders-container--text{width:95vw;padding-left:.5rem;font-size:14px;line-height:17px}img{width:90vw;justify-self:center}}